* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Georgia, 'Times New Roman', Times, serif;
  letter-spacing: 0.012em;
  line-height: 1.21053;
}

header {
  background-color: #0fadad;
  color: #fff;
  display: flex;
  flex-flow: row nowrap;
  height: 48px;
  position: fixed;
  justify-content: space-between;
  text-align: center;
  top: 0;
  width: 100%;
  z-index: 50;
}

.header-main {
  box-shadow: 1px -6px 15px 1px black;
}

header Link {
  display: inline-block;
}

.logo-text {
  color: #fff;
  display: inline-block;
}

.logo {
  cursor: pointer;
  font-size: 22px;
  height: 28px;
  margin-top: 11px;
}

.logo-icon {
  color: #fff;
  margin-right: 5px;
}

.app-container {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  position: relative;
}

.text {
  font-size: 19px;
  min-height: 100%;
}

.menu {
  background-color: #fff;
  display: inline;
  height: 100%;
  margin-top: 48px;
  position: fixed;
  text-align: center;
  top: 0;
  width: 100%;
  z-index: 49;
}

a,
a:active,
a:visited {
  color: #000;
  text-decoration: none;
}

li {
  list-style: none;
  margin: 10px 10px;
}

.menu li a {
  border-bottom: 1px solid #ccc;
  display: inline-block;
  font-size: 125%;
  height: 40px;
  text-align: left;
  width: 75%;
}

li a div {
  margin: 9.2px 0;
}

footer {
  background-color: rgb(10, 10, 10);
  color: #ccc;
  margin-top: auto;
  padding: 11.5px 0;
  text-align: center;
}

.menu-icon-placeholder,
.icon-placeholder {
  display: inline-block;
  min-width: 48px;
}

.menu-icon {
  cursor: pointer;
  display: inline;
  font-size: 28px;
  margin: 10px 0;
}

.home {
  margin: 20px 50px;
  padding-top: 48px;
}

.book-search {
  margin-top: 40px;
  padding-top: 48px;
  text-align: center;
}

.book-search h1 {
  font-size: 28px;
}

.book-search form {
  height: 34px;
  width: 85%;
}

.book-search input {
  border: 1px solid rgb(134, 134, 134);
  display: inline-block;
  height: 34px;
  outline: none;
  padding: 5px 5px;
  width: 330px;
}

.book-search button {
  background-color: #ffc524;
  border: none;
  cursor: pointer;
  display: inline-block;
  height: 34px;
  outline: none;
  padding: 2.5px 7.5px;
  text-transform: uppercase;
}

.book-search form {
  display: inline-block;
  margin: 25px 0;
}

.grid-container {
  display: grid;
  flex-flow: column nowrap;
  grid-template-columns: 50% 50%;
  margin: 0 auto;
}

.book {
  margin: 25px 0;
  text-align: center;
}

.bookPlaceholder {
  background-color: rgb(248, 248, 248);
  box-shadow: 0px 0px 2px 0.5px rgb(212, 212, 212);
  display: block;
  margin: 0 auto;
  padding: 10px;
  transition: box-shadow 0.5s;
  width: 152px;
}

.bookPlaceholder:hover {
  box-shadow: 0px 0px 5px 0.5px rgb(131, 131, 131);
}

.book img {
  height: 200px;
  width: 132px;
}

.author {
  font-size: 13px;
}

.title {
  margin: 5px 0;
  font-size: 16px;
}

.bookInfo {
  text-align: left;
  width: 132px;
}

.desktop-menu {
  display: none;
  font-size: 125%;
}

.desktop-menu ul {
  margin: 12px 0;
}

.desktop-menu li {
  margin: 0 50px;
}

@media (min-width: 635px) {
  .grid-container {
    grid-template-columns: 33.3% 33.3% 33.3%;
    width: 90%;
  }
}

@media (min-width: 952px) {
  .grid-container {
    grid-template-columns: 25% 25% 25% 25%;
  }
}

@media (min-width: 1060px) {
  .grid-container {
    width: 80%;
  }

  .desktop-menu {
    display: inline;
  }

  .desktop-menu li {
    display: inline;
  }
  .icon-placeholder {
    min-width: 310px;
  }
  .menu-icon {
    display: none;
  }

  .menu {
    display: none;
  }

  .desktop-menu a,
  .desktop-menu a:active,
  .desktop-menu a:visited {
    color: #fff;
  }
}

@media (min-width: 1400px) {
  .grid-container {
    width: 60%;
  }
}
